import marked from 'marked';
import './Preview.css';
 import { useState } from 'react';
import ExpandColaps from '../ExpandColaps/ExpandColaps';

const Preview  = (props) => {
    const [style,setStyle] = useState(null)
 const changeSize = (ExpandColaps) =>{
    ExpandColaps? setStyle():
     setStyle({width: '90%',minHeight:'450px',tdisplayop:'block', margin:'0 auto' ,position: 'absolute' , top:'0',left:'5%' ,zIndex:'1' });
 }
    return ( 
        <div>
            {/* <div id = "preview" dangerouslySetInnerHTML={{__html:marked(this.props.text)}}></div> */}
         <div style={style} className="preview">
             <div className='toolbar'  >Preview
                 <ExpandColaps changeSize={ExpandColaps=> changeSize(ExpandColaps)} /> </div>
             <div className="workPlace"  dangerouslySetInnerHTML={{__html:marked( props.input)}}></div>
         </div>
         
        </div>
     );
}
 
export default Preview ;