import { Component } from "react";
import Alarm from "./Alarm/Alarm";
import './App.css'
import Calculator from "./Calculator/Calculator";
import Drum from "./Drum/Drum";
import MarkdownPreviewer from "./MarkdownPreviewer/Markdown Previewer";
import RandomQuote from "./RandomQuote/RandomQuote";

class App extends Component{

render(){
    return(
        <div>
            <nav className='navbar'><h1>Freecodecamp React Apps</h1></nav>
            <div className="Apps">
                 <div className='eachApp'><RandomQuote /></div>
                <div className='eachApp'><MarkdownPreviewer/></div>
                <div className='eachApp'> <Drum/> </div> 
                <div className='eachApp'> <Calculator/> </div>
                <div className='eachApp'> <Alarm/> </div>
            </div>
        </div>
    )
}
}

export default App;